<template>
  <div class="content">
    <h1>20年了，还是周杰伦</h1>
    <p>近日，AppleMusic公布了2021年全球各大市场音乐年终榜单，其中中国大陆地区音乐TOP100排行榜中，周杰伦以超过半数的54首作品霸榜。</p>
    <p>人们不禁感慨，这是2021年的榜单还是2001年的榜单？去年这个榜单公布时，周杰伦以47首歌曲入围TOP100，过了一年，还多了7首。</p>
    <p>54首歌中有不少是周杰伦于15年前甚至20年前创作的作品，那么这20年来，华语流行乐坛经历了什么？</p>
    <p>偶像冷却、选秀退场、版权大战平息之后，华语流行乐坛仍处于相当疲软的状态中。</p>
    <p>
      <strong>榜单说明不了全部</strong>
    </p>
    <p>单纯通过AppleMusic的榜单分析，绝大部分人都会发出华语乐坛后继无人的观点，但事实上并非如此。</p>
    <p>虽然说服务于苹果生态的AppleMusic界面清新、排版整齐、推荐实用、播放音乐时还能显示完整的专辑封面，但是具有离线缓存bug、平台壁垒等问题，以及不具备国产数字音乐软件强大社交功能。</p>
    <p>在本土用户数量和同类选手相比不占优势的情况下，AppleMusic的榜单不能说明所有问题，毕竟还有大量受众压根没有用过。</p>
    <p>翻看QQ音乐12月2日更新的榜单，热歌榜前20名中，周杰伦的歌曲同样占据7席，至于网易云音乐，它们没有周杰伦歌曲的版权。</p>
    <p>这样看来，虽然周杰伦上一首单曲作品《Mojito》已是一年半前的作品了，但是他在听众心中还是那个“华语乐坛之王”。</p>
    <p>仔细看那个周杰伦霸榜的榜单，除了他的54首歌曲之外，最多出现的名字是林俊杰和陈奕迅，莫文蔚、田馥甄、吴青峰、蔡健雅、林宥嘉、孙燕姿等人同样占据榜单之中。</p>
    <p>年轻歌手中仅有1998年出生的黄霄云以《星辰大海》位列榜单，剩下的内地歌手如郭顶、李荣浩等人，最年轻的也是上世纪80年代生人的歌手了。</p>
    <p>看到这里令人心生疑惑，那些长期霸占社交媒体、热搜、各种APP首页广告位、各种快消产品代言位的新生代歌手的作品，为什么没有一个位列其中？那么多音乐综艺节目，每个都以“爆款”作为宣传点，怎么也没有一首作品位列其中？</p>
    <p>
      <img class="content-picture" src="~images/homeImg/swiperImg/1.jpg" />
    </p>
    <p>两年前，一场周杰伦与某流量歌手的打榜大战让人再次见识到了周杰伦的威力，豆瓣小组一句“周杰伦微博数据那么差，为什么演唱会门票还难买啊”的灵魂发问，激怒了周杰伦的歌迷，随后成立了夕阳红粉丝团，把周杰伦的超话排名稳稳送上了榜首，最终周杰伦以“1.1亿”的影响力超越了该流量歌手，并刷新了超话榜纪录。</p>
    <p>42岁的周杰伦霸榜，对于他而言当然是值得骄傲的事情，但是对于华语乐坛而言，新生代处于更好的传播环境，却缺乏叫得响亮、传播度高的艺术作品，这也是不争的事实。</p>
    <p>疫情期间，五月天、陈奕迅、孙燕姿的演唱会直播，同样获得了大量流量和粉丝的支持，人们一边泪流满面地听着自己的青春岁月，一边不忘向当下的流行乐坛啐上一口。</p>
    <p>“什么人也出来唱歌，他没有这个能力啊！”</p>
    <p>冷静下来，不妨思考另外一个问题，假如周杰伦在当下这个时代出道，凭借他的才华，是否能够获得当下的荣誉？</p>
    <p>虽然历史没有如果，但这个假设确实会令人犹豫。</p>
    <p>
      <strong>时代的变与不变</strong>
    </p>
    <p>从颜值看，无论是在20年前，还是今天，周杰伦放人堆里绝对不是那个最亮眼的，好在那个年月人们还没有像今天这般推崇“颜值即是正义”。</p>
    <p>从音乐天赋和才华看，无论是在20年前，还是今天，周杰伦放人堆里绝对是那个最亮眼的，好在这个年月我们听音乐还是要看才华。</p>
    <p>周杰伦的出道和走红，有着特殊的时代背景和历史原因，音乐产业高度发达，西方先进的编曲制作技术和设备被引用和使用，老一批港台流行歌手逐渐隐退，一批高水平的音乐人和音乐作品在那个时代井喷而出。</p>
    <p>周杰伦的创作是超前的，无论是R&B和Hip-Hop的使用，还是古典和流行的结合，中国风的加入，以及带有时代性和人文关怀的歌词内容，这些都让周杰伦在出道之时显得与众不同。正是这种创作上的超前意识，让他在出道之时饱受非议，但独特的声线和与众不同的曲风，让他得以在一个“群雄割据”的格局下显得特别。</p>
    <p>去年，在周杰伦和某卫视打造的综艺节目《周游记》中，周杰伦在新加坡给一位外国小伙听了一首《双截棍》，并询问其认为这首歌曲创作了几年了。对方回答：5年，但是，这是一首周杰伦于19年前创作的歌曲，这能部分说明他在创作上的超前。</p>
    <p>和巅峰香港乐坛大量出色歌手不同，周杰伦完全使用国语演唱，由于祖籍是福建，还熟练掌握闽南语，这让那时对于流行音乐如饥似渴的大陆受众如获至宝，对于内地唱片工业的市场开发同样重要。</p>
    <p>爆红之后的商业合作纷至沓来，通讯服务商、网络游戏、服装食品、周杰伦的商业版图至今仍在拓展，水涨船高的市场价值带来了极高的曝光度和影响力，在传播途径有限，方式相对单一的曾经，周杰伦的作品迅速席卷了大江南北。</p>
    <p>在二十一世纪初期，周杰伦便开始接触影视，从在《寻找周杰伦》中扮演自己，到《头文字D》中饰演藤原拓海、《满城尽带黄金甲》饰演元杰，再到自导自演的《不能说的秘密》、《天台爱情》，其中还有和好莱坞合作的《青蜂侠》和《惊天魔盗团2》，周杰伦的多栖发展的速度要远快于同时期的唱作型歌手。</p>
    <p>
      <img class="content-picture" src="~images/homeImg/swiperImg/1-2.jpg" />
    </p>
    <p>以上这些，对于当下时代的流量艺人来说，只要资本足够，都不叫事。有自诩为歌手的艺人，歌曲还没发，影视作品已经上了，综艺节目的邀约也应接不暇，据悉为了获得某年轻歌手的代言，商家和经纪人预约洽谈都要排队到半年之后。</p>
    <p>真正让周杰伦坚挺的却不仅仅是商业上的成功和多栖跨界寻找的产业融合，而是作品的持续生产。从2000年至今，周杰伦14张正式专辑和多首单曲的发布，跨越了两个十年，算上给其他歌手创作和影视歌曲，周杰伦约有400首作品出现在华语乐坛。虽然这两年其产量有所降低，更多的则是应对当下的音乐传播和产业模式，而并非真如某些网友所说的“江郎才尽”。</p>
    <p>
      <img class="content-picture" src="~images/homeImg/swiperImg/1-3.jpg" />
    </p>
    <p>“我是一个音乐人”，这让周杰伦变得很纯粹，可以说他电影演得不好、代言有些中二，但没有人会说，他的音乐不好，这就是一个音乐人的坚持和底线。</p>
    <p>坚持创作持续产出优秀的音乐作品，这是资本无法弥补的，时代再如何改变，作品仍然是实打实的。</p>
    <p>在“超女”“快男”火起来以后，不少人认为综艺是华语音乐最后一根稻草，但事实上，真正的歌手没有几个是综艺咖，而化身综艺咖后的歌手，除了黑料和丑闻频率直线上升以外，作品的水平并没有太多提升，真正的音乐正在被边缘化，而“秀”和“选”正在突出。</p>
    <p>很多人不知道，真正的音乐受众，是不会因为某个歌手长得帅、或者会营销，人设好、或者流量大，就去每天听他、唱他的歌的。</p>
    <p>但人红歌不红，是当下不少人追求的目标，因为歌红带不来人民币，人红才是一本万利。</p>
    <p>当面对质疑的时候，很多粉丝常为偶像说这样一句话：“你知道XXX有多努力么？”</p>
    <p>事实上，从来没有人为周杰伦说过这样的话，因为他有多努力，大家都看到了。这可能才是两代音乐人最大的不同，环境只是时代的注脚，而个人的选择和判断，是从来没有改变的。</p>
    <p>
      <strong>下一个周杰伦</strong>
    </p>
    <p>每次这样的榜单问世引发讨论，都会有人提出这个问题：华语乐坛什么时候能出现下一个周杰伦？</p>
    <p>这个问题很难回答，在成名之前，很多歌手都会梦想着自己成为下一个周杰伦。在一段时间内，某些歌手的商业价值已经超过了周杰伦，但是从音乐天赋上来看，他们距离周杰伦仍然有差距。</p>
    <p>单纯从音乐天赋上看，在一定的人口基数的比例下，下一个具有周杰伦这般音乐天赋的年轻人必然会出现，但是能否达到周杰伦这样的高度，在当下这个时代，已经不再是绝对由天赋决定的了。</p>
    <p>天赋和市场能否融合，这是一个多方努力的结果，歌手本身有天赋，有相对包容的环境提供创作条件、有相对公平的传播方式使其传播、有相对健康的曝光模式令其成长，还要有理性且有购买力的市场作为支撑，这些缺乏哪一点，都可能使得音乐人和音乐本身跑偏，所以这样的多方合力，目前看并不存在，未来是否存在，也很难说。</p>
    <p>大批曾经高光的唱片公司，成了互联网音乐平台的附庸产品，市场上再也没有了过去动辄百万以上的唱片销量。唱片公司被收购，唱片店倒闭，曾经的巨头靠变卖版权勉强度日。</p>
    <p>就连曾经作为流行音乐产业生态中重要部分的KTV，也被年轻人所抛弃，成为了老年歌友会的场所。随便推开一扇KTV的门，里面传来的不再是《晴天》或是《十年》而是《鸿雁》和《敖包相会》。</p>
    <p>网易云音乐虽然要上市了，但是依然是亏损的状态，腾讯音乐的股价已经跌破发行价，娱乐多元化大大挑战了音乐的原有地位。长短视频、手机游戏、网络小说等文艺作品得以发展壮大，留给大众听一段音乐的时间越来越少。</p>
    <p>市场没法重振，就会面临着没有土壤滋养创作者，创作者们只能自己养活自己，在这样的环境下，不能去诟病年轻歌手的态度和能力，因为他们也是为了生存。</p>
    <p>在综艺节目《向往的生活》中，演员陈赫向歌手张艺兴提出了一个问题，“为什么现在没有好听的歌了？是我们老了么？”</p>
    <p>张艺兴的回答是：“音乐从头到尾就只有那么几个音符，好听的旋律都被人唱完了，导致现在的音乐人，做起旋律来特别困难，稍不注意就会和前人雷同。”</p>
    <p>当然，张艺兴的话有些绝对，但是有一点是必然的，就那么几个音符，想排列好让人感到舒适，不是一件容易的事。想要传唱20年，还能霸榜，就更不是一件容易的事了。</p>
    <p>2018年，周杰伦发布了单曲《等你下课》，其中有这样两句歌词：</p>
    <p>“你耳机听什么，能不能告诉我？”</p>
    <p>很多歌迷想问的是：“你什么时候出新专辑，能不能告诉我？”</p>
